body {
  background-color: #000000;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

label {
  color: #ffffff;
}

.AddMovie {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 100px auto 40px auto;
  position: relative;
}

.EditMovie {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 100px auto 40px auto;
  position: relative;
}

.Home {
  margin: 100px auto;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  max-width: 1200px;
  color: #ffffff;
}

.Movie {
  background-color: #212529 !important;
  color: #ffffff;
  text-decoration: none !important;
  margin: 30px auto;
  padding: 20px 20px;
  border-radius: 20px !important;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.Movies {
  margin: 80px auto;
  position: relative;
}

.MovieTable {
  margin: 80px auto;
  position: relative;
}

.Profile {
  margin: 0px auto;
  text-align: center;
}

.ShowMovie {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 100px auto 40px auto;
  position: relative;
  color: #ffffff;
}

.app-title {
  font-weight: bold;
  color: #ffffff !important;
  margin: 30px auto;
}

.app-title:hover {
  cursor: default;
}

.descriptionLogo {
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 5%;
  width: 300px;
}

.heading {
  margin: 20px 10px 20px 10px;
  color: #ffffff;
}

.image-container {
  width: 200px;
  height: 300px;
}

.loader {
  background-color: #000000;
  height: 75vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader-spinner {
  width: 5rem;
  height: 5rem;
}

.logo {
  margin-bottom: 20px;
  border-radius: 5%;
  width: 300px;
}

.main-content {
  flex: 1 1;
  text-align: center;
}

.movie-button {
  margin: 0px 5px 5px 5px;
}

.movie-buttons {
  margin-top: 20px;
}

.movie-details {
  margin-left: 10px;
}

.movie-directors {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.movie-image {
  height: 200px;
  width: 200px;
  overflow: hidden;
}

.movie-info {
  margin-left: 5px;
  width: 250px;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.movie-link {
  border: none;
  outline: none;
  text-decoration: none !important;
  color: #ffffff;
}

.movie-link:hover {
  text-decoration: none !important;
  color: #808080;
}

.movie-list {
  margin: 10px 30px;
}

.movie-poster {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.movie-title {
  overflow: hidden;
  white-space: normal;
  text-overflow: ellipsis;
}

.movie-title-link {
  font-weight: bold;
  font-size: 24px;
  color: #ffffff !important;
  text-decoration: none !important;
}

.movie-title:hover {
  color: #046cfb !important;
}

.navBar {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}

.navLogo {
  width: 50px;
  height: 50px;
  border-radius: 10%;
}

.nav-links {
  color: #ffffff !important;
  text-align: center;
}

.poster {
  width: 300px;
  margin: 0px 0px 40px 0px;
}

.profile-links {
  text-align: center;
}

.side-panel {
  flex: 1 1;
  background-color: #212529;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px #000000;
  color: #ffffff;
}

.side-panel-title {
  font-size: 1.8rem;
  margin-top: 20px;
  margin-bottom: 40px;
  color: #ffffff;
  text-shadow: 0 1px 3px #000000;
}

.side-panel-description {
  font-size: 1rem;
  line-height: 1.5;
  color: #b0b0b0;
}

#logout {
  color: #ff0000;
}

#logout:focus {
  background-color: transparent;
}

#userPicture {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 25px;
}

@media only screen and (max-width: 768px) {
  .MovieTable {
    margin: 0px auto;
    position: relative;
    width: 80%;
  }

  .Movie {
    background-color: #212529 !important;
    color: #ffffff;
    text-decoration: none !important;
    margin: 30px 0px;
    padding: 20px 20px;
    border-radius: 20px !important;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .loader {
    background-color: #000000;
    height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .movie-buttons {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }

  .movie-details {
    margin: 10px auto 0px auto;
    text-align: center;
  }

  .image-container {
    width: 200px;
    height: 300px;
    margin: 0px auto;
  }
}

@media only screen and (max-width: 600px) {
  .MovieTable {
    margin: 80px auto;
    position: relative;
    width: 90%;
  }
}

@media only screen and (max-width: 400px) {
  .MovieTable {
    margin: 80px auto;
    position: relative;
    width: 100%;
  }
}

